<template>
  <div class="home-page">
   <div class="container">
      
      <div class="card">
        <div class="first-page">
          啊啊啊啊啊啊啊啊啊啊啊啊啊啊
          <div id="wei1">wei1</div>
          <div type="button" onclick="alertInfo(1)">打开1</div>
          <div id="btn" type="button" class="btn-change btn-blue" @click="changePage(2)">点开惊喜</div>
          <!-- <div id="btn" type="button" class="btn-change btn-white" onclick="changePage(2)">点开惊喜</div> -->
        </div>
        <div class="second-page">
          <div class="rongqi">
            <div class="wish-div">
              <div id="girl-name">XXX</div>
              <div class="text-content">在这美好的日子里祝你节日快乐，工作顺利。</div>
              <div class="text-content">在这美好的日子里祝你节日快乐，工作顺利。</div>
            </div>
          <div id="wei2">wei2</div>
          <div type="button" @click="alertInfo(2)">打开2</div>
          <div id="btn" type="button" class="btn-change btn-blue" @click="changePage(1)">返回封面</div>
            <div class="button-div">
            <!-- <div id="btn" type="button" class="btn" onclick="receiveRedBag(1)">返回封面</div> -->
              <button id="btn" type="button" class="btn" @click="receiveRedBag()">开</button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div id="play-btn">
      <!-- <img src="./music.png"  alt="音乐" /> -->
    </div>
    <!-- <audio id="mp3" src="./music.mp3" autoplay loop="loop" ></audio> -->
  </div>
</template>

<script>
export default {
  data () {
    return {
        snowflakeURl: [
        // './flower1.png',
        // './flower2.png',
        // './flower3.png',
        // './flower4.png',
        // './flower5.png',
        // './flower6.png'
        ]
    }
  },
  methods: {
    alertInfo(num) {
      document.getElementById('wei1').innerHTML = '打开' + num
      document.getElementById('wei2').innerHTML = '打开' + num
      alert('打开' + num)
    },
    changePage(page) {
      // 翻到第二页
      if (page === 2) {
        document.getElementsByClassName('card')[0].style.transform = 'rotateY(180deg)'
      } else {
        document.getElementsByClassName('card')[0].style.transform = 'rotateY(0)'
      }
    }
  }
}
</script>

<style lang="scss">
    #flower-container{
      height: 100%;
      width: 100%;
      overflow: hidden;
      position: absolute;
    }
    .container{
      height: 100vh;
      width: 100vw;
      position: relative;
      perspective:1000;transform-style:preserve-3d;
    }
    .card{
      position: relative;
      transition:1s;transform-style:preserve-3d;
    }
    .first-page, .second-page{
      height: 100vh;
      width: 100vw;
      -webkit-overflow-scrolling: touch;
      position: absolute;
      backface-visibility:hidden;
      background-size: 100% 100%;
    }
    .first-page{
      z-index: 2;
    //   background-image: url('./background1.jpg');
    }
    .second-page{
    //   background-image: url('./background2.png');
      transform:rotateY(-180deg);
    }
    .btn-white{
      color: white;
      border: 2px solid white;
      animation: animated-border-white 1.5s infinite;
    }
    .btn-blue{
      color: #6986a5;
      border: 2px solid #6986a5;
      animation: animated-border-blue 1.5s infinite;
    }
    .btn-change{
      width: 100px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      position: absolute;
      bottom: 30px;
      right:30px;
      border-radius: 10px;
    }
    @keyframes animated-border-white{
      0%{
        box-shadow: 0 0 0 0 rgba(255,255,255,0.7);
      }
      100%{
        box-shadow: 0 0 0 5px rgba(255,255,255,0);
      }
    }
    @keyframes animated-border-blue{
      0%{
        box-shadow: 0 0 0 0 rgba(105,134,165,0.7);
      }
      100%{
        box-shadow: 0 0 0 5px rgba(105,134,165,0);
      }
    }
    /* .container:active .card{transform:rotateY(180deg);} */
    .rongqi{
      padding-top: 65%;
    }
    .wish-div{
      margin: 0 auto;
      width: 60%;
      /* height: 300px; */
      background-color: #fff;
      opacity: 0.6;
      border-radius: 4px;
      padding: 20px;
      font-weight: bold;
      line-height: 2rem;
    }
    #girl-name{
    }
    .text-content{
      text-indent: 40px;
    }
    .button-div{
      /* margin-top: 50px; */
    }
    .btn{
      outline: 0;
      font-weight: bold;
      width: 3em;
      height: 3em;
      color: #fff;
      border: none;
      display: block;
      font-size: 1.5em;
      margin: 2rem auto;
      background-color: #ffd955;
      border-radius: 100%;
      transition: background-color 1s, transform 1.5s;
    }
    .btn:active{
      transform: scale(1.3);
      background-color: #ffc600;
    }
    #play-btn {
      position: absolute;
      top: 16px;
      right: 20px;
      z-index: 30;
      width: 34px;
      height: 34px;
      text-align: center;
      border-radius: 50%;
      -webkit-transition-duration: 1s;
      -webkit-animation: rotate 3s linear infinite;
      animation: rotate 3s linear infinite;
      animation-play-state: paused;
      color: #efefef;
      box-shadow: 1px 2px 2px #777;
    //   background-image: url('./music.png');
      background-size: contain;
    }
    #play-btn img{
      width: 30px;
      height: 30px;
    }
    @-webkit-keyframes rotate { 
      from {
        -webkit-transform: rotate(0deg)
      }
      to {
        -webkit-transform: rotate(360deg)
      }
    }
    @keyframes rotate{
      from {
        transform: rotate(0deg)
      }
      to {
        transform: rotate(359deg)
      }
    }
</style>



